/* OrganizationPage.css */

.profile-content {
  padding: 20px;
  font-size: 16px;
  line-height: 1.6;
}

.profile-description {
  white-space: pre-line;
}

.organization-description {
  font-size: 0.95rem;
  line-height: 1.6;
  color: #000000;
  margin-left: 4 0px;
}

.organize-title {
  font-weight: bold;
  color: #000000;
  font-size: 1.3rem;
  margin-bottom: 10px;
  padding-bottom: 5px;
  border-bottom: 2px solid #e60000; /* Garis merah di bawah */
  display: inline-block; /* Biar garis hanya sepanjang teks */
  width: 550px;
}

