/* ===== Profile Page ===== */


.section-label {
  font-size: 1rem;
  font-weight: bold;
  color: #ffffff;
}

.name-info {
  margin-left: 15px;
  margin-top: -5px;
}

.profile-content {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 60px;
  flex-wrap: wrap;
}

.profile-photo {
  width: 250px;
  height: 250px;
  object-fit: cover;
  border-radius: 50%;
  border: 4px solid #000000;
  box-shadow: 0 4px 10px rgba(255, 0, 0, 0.15);
  margin-left: 30px;
}

.profile-description {
  max-width: 600px;
  font-size: 0.95rem;
  line-height: 1.6;
  color: #333;
}

/* ===== Responsive Mobile ===== */
@media (max-width: 600px) {
  .profile-content {
    flex-direction: column;
    text-align: center;
  }

  .left-header {
    flex-direction: column;
    align-items: flex-start;
  }

  .section-label {
    font-size: 0.9rem;
  }
}

