/* ===== Welcome Page ===== */
.welcome-section {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 0 20px;
  background: linear-gradient(to right, #ff0000, #ff0000);
  color: white;
}

.welcome-text {
  font-size: 2rem;
  max-width: 600px;
  margin-bottom: 30px;
}

.explore-button {
  padding: 12px 24px;
  background-color: #fff;
  color: #764ba2;
  border: none;
  border-radius: 25px;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
}

.explore-button:hover {
  background-color: #d3cce3;
  transform: scale(1.05);
  box-shadow: 0px 4px 10px rgba(0,0,0,0.2);
}
