/* EducationPage.css */

.education-page {
  transition: margin-left 0.3s ease;
}

.education-page.sidebar-open {
  margin-left: 200px; /* Sesuaikan dengan lebar sidebar kamu */
}

.profile-content {
  padding: 20px;
  font-size: 16px;
  line-height: 1.6;
}

.profile-description {
  white-space: pre-line;
}

.page-description {
  font-size: 0.95rem;
  line-height: 1.6;
  color: #000000;
  margin-left: 40px; /* diperbaiki dari "4 0px" jadi "40px" */
}

.edu-title {
  font-weight: bold;
  color: #000000;
  font-size: 1.3rem;
  margin-bottom: 10px;
  padding-bottom: 5px;
  border-bottom: 2px solid #e60000; /* Garis merah di bawah */
  display: inline-block; /* Biar garis hanya sepanjang teks */
  width: 550px;
}

.gpa {
  background-color: #000;
  color: #ffffff;
  padding: 3px 8px;
  border-radius: 5px;
  font-weight: 600;
}
