/* SkillsPage.css */

.profile-content {
  padding: 20px;
  font-size: 16px;
  line-height: 1.6;
}

.profile-description {
  white-space: pre-line;
}

.skills-description {
  font-size: 0.95rem;
  line-height: 1.6;
  color: #000000;
  margin-left: 20px;
}

.skills-title {
  font-weight: bold;
  color: #000000;
  font-size: 1.3rem;
  margin-bottom: 10px;
  padding-bottom: 5px;
  border-bottom: 2px solid #e60000; /* Garis merah di bawah */
  display: inline-block; /* Biar garis hanya sepanjang teks */
  width: 550px;
}

.skills-page {
  transition: margin-left 0.3s ease;
  margin-left: 0;
}

.skills-page.sidebar-open {
  margin-left: 250px; /* sesuaikan dengan lebar sidebarmu */
}

.hamburger.in-header {
  position: fixed;
  top: 20px;
  left: 20px;
  z-index: 2000;
  background-color: black;
  color: white;
  padding: 6px 12px;
  font-size: 24px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
}


.skills-description {
  padding:30px;
  font-family: Arial, sans-serif;
}

.skills-title {
  font-size: 24px;
  color: #333;
}

.skills-list1 {
  list-style-type: disc;
  padding-left: 40px;
  margin-top: 10px;
  margin-bottom: 20px;
}


.skills-list {
  list-style-type: disc;
  padding-left: 40px;
  margin-top: 10px;
  margin-bottom: 20px;
}

.skills-item {
  margin-bottom: 8px;
  font-size: 16px;
  color: #444;
}

.skills-description h3 {
  margin-top: 20px;
  color: #222;
  font-size: 20px;
  margin-left: 15px; 
  border-bottom: 2px solid #ddd;
  padding-bottom: 5px;
}

