/* ===== Sidebar Navigation ===== */


/* ===== Header Sidebar ===== */

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 2px solid #ffffff;
  padding-bottom: 10px;
  margin-bottom: 30px;
  background-color: #0062ff;
  color: #fff;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
}

.left-header .name-info h2 {
  margin: 0;
  margin-left: 50px;
  font-size: 30px;
}

.name-info p {
  margin: 0;
  margin-left:55px;
  font-size: 0.8rem;
  color: #ffffff;
}

.left-header {
  display: flex;
  align-items: center;
  gap: 15px;
}

/* ===== Sidebar Navigasi ===== */
.sidebar {
  position: fixed;
  top: 2;
  left: -250px;
  width: 250px;
  height: 100vh;
  background-color: #000000;
  color: white;
  padding-top: 20px;
  box-shadow: 2px 0 5px rgba(0,0,0,0.3);
  transition: left 0.3s ease;
  z-index: 1500px;
  display: flex;
  flex-direction: column;
}

.sidebar.open {
  left: 0;
}

.sidebar ul {
  list-style: none;
  padding-left: 20px;
  margin: 0;
}

.sidebar ul li {
  padding: 15px 0;
  font-size: 1.1rem;
  cursor: pointer;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  transition: background-color 0.2s ease;
}

.sidebar ul li:hover {
  background-color: rgba(255, 255, 255, 0.15);
}

/* Margin shift saat sidebar terbuka */
.profile-page.sidebar-open .header,
.profile-page.sidebar-open .profile-content {
  margin-left: 250px;
}

.header, .profile-content {
  transition: margin-left 0.3s ease;
}


.hamburger.sidebar-hamburger {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  top: 12px;
  margin: 0 60px 50px 20px; /* jarak dari sisi sidebar dan bawah */
  width: 40px;       /* ukuran kotak tombol */
  height: 40px;
  font-size: 24px;   /* ukuran icon 3 garis */
  cursor: pointer;
  user-select: none;
  padding: 0;        /* padding dihilangkan supaya kotak tombol pas */
  border-radius: 6px; /* sudut agak membulat */
  background-color: #ff0000;  /* warna lebih gelap agar kontras */
  color: rgb(0, 0, 0);
  border: none;
  transition: background-color 0.3s ease, transform 0.2s ease;
  z-index: 1100;
}

.hamburger.sidebar-hamburger:hover {
  background-color: #ffffff;
  transform: scale(1.1);
}